/**
 * @famgia/omnify-japan
 * Japan-specific types plugin for omnify-schema
 *
 * Provides custom types for Japanese data formats:
 * - JapanesePhone: Japanese phone number format
 * - JapanesePostalCode: Japanese postal code format
 * - JapaneseAddress: Compound type for full Japanese address
 * - Prefecture: Japanese prefecture enum
 */

import type { OmnifyPlugin, CustomTypeDefinition, PluginEnumDefinition } from '@famgia/omnify-types';
import {
  prefectureLabels,
  bankAccountTypeLabels,
  enumDisplayNames,
  PREFECTURE_KEYS,
  PREFECTURE_AREAS,
} from './locales.js';

// Re-export locales for external usage
export * from './locales.js';

// Re-export types
export type { PrefectureKey } from './locales.js';

/**
 * Japanese prefecture labels (都道府県名)
 * Display names in Japanese
 * @deprecated Use prefectureLabels from locales.ts instead
 */
export const PREFECTURE_LABELS = PREFECTURE_KEYS.map(key => prefectureLabels[key]?.ja ?? key);

/** @deprecated Use PREFECTURE_KEYS instead */
export const PREFECTURES = PREFECTURE_LABELS;
/** @deprecated Use PrefectureKey instead */
export type Prefecture = string;

/**
 * JapanesePhone type definition
 * Format: 090-1234-5678 or 03-1234-5678
 */
const japanPhoneType: CustomTypeDefinition = {
  name: 'JapanesePhone',
  description: 'Japanese phone number (e.g., 090-1234-5678)',
  sql: {
    sqlType: 'VARCHAR',
    length: 15,
  },
  typescript: {
    type: 'string',
  },
  // Only base fields allowed (no type-specific fields)
  validFields: [],
  validate: (_prop) => {
    // Optional validation logic
    return undefined;
  },
};

