/**
 * BankAccountType enum definition - Japanese bank account types
 */
const bankAccountTypeEnum: PluginEnumDefinition = {
  name: 'BankAccountType',
  displayName: enumDisplayNames.BankAccountType,
  description: 'Japanese bank account types (Zengin system codes)',
  values: [
    { value: '1', label: bankAccountTypeLabels['1']! },
    { value: '2', label: bankAccountTypeLabels['2']! },
    { value: '4', label: bankAccountTypeLabels['4']! },
  ],
};

/**
 * Prefecture enum definition - all 47 Japanese prefectures
 * Default: String enum values (hokkaido, tokyo, saitama, ...)
 * Areas: hokkaido, tohoku, kanto, chubu, kinki, chugoku, shikoku, kyushu
 */
const prefectureEnum: PluginEnumDefinition = {
  name: 'Prefecture',
  displayName: enumDisplayNames.Prefecture,
  description: 'Japanese prefectures (string enum)',
  values: PREFECTURE_KEYS.map((key, index) => ({
    value: key,
    label: prefectureLabels[key]!,
    extra: { area: PREFECTURE_AREAS[key], code: index + 1 },
  })),
};

/**
 * PrefectureCode enum definition - all 47 Japanese prefectures (numeric JIS codes)
 * For legacy systems that use numeric IDs (1-47)
 */
const prefectureCodeEnum: PluginEnumDefinition = {
  name: 'PrefectureCode',
  displayName: enumDisplayNames.PrefectureCode,
  description: 'Japanese prefectures (JIS X 0401 numeric codes)',
  values: PREFECTURE_KEYS.map((key, index) => ({
    value: String(index + 1),
    label: prefectureLabels[key]!,
    extra: { area: PREFECTURE_AREAS[key] },
  })),
};

/**
 * @famgia/omnify-japan plugin definition
 */
export const japanTypesPlugin: OmnifyPlugin = {
  name: '@famgia/omnify-japan',
  version: '0.0.1',
  types: [
    japanPhoneType,
    japanPostalCodeType,
    japanAddressType,
    japanNameType,
    japanBankAccountType,
  ],
  enums: [bankAccountTypeEnum, prefectureEnum, prefectureCodeEnum],
  setup: (context) => {
    context.logger.debug('Japan types plugin loaded');
  },
};

/**
 * Default export for easy importing
 */
export default japanTypesPlugin;

/**
 * Re-export types for type-safe usage
 */
export type { OmnifyPlugin, CustomTypeDefinition } from '@famgia/omnify-types';
